.default--title {
  color: #23b5d3;
  font-size: 40px;
}

.default--input {
  background: white;
  border: #23b5d3 solid 2px;
  border-radius: 7px;
  height: 30px;
  margin-top: 5px;
  padding: 7px;
}

.default--select {
  background: white;
  border: #23b5d3 solid 2px;
  border-radius: 7px;
  height: 45px;
  margin-top: 5px;
  padding: 7px;
}

.default--button {
  background: #23b5d3;
  border: none;
  border-radius: 7px;
  font-size: 16px;
  height: 35px;
  width: 170px;
}

.button {
  height: 50px;
}

.default--button:hover {
  background: #3cd0ee;
}

.default--button:active {
  border: white solid 1px;
  padding: 8px;
}

.default--input-label {
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont,
    Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell,
    Open Sans, Helvetica Neue, sans-serif Georgia , Times , serif;
  font-size: 18px;
  margin: 8px 5%;
  width: 40%;
}

.description {
  width: 100%;
}

.default--erase-btn {
  background: red;
  border: none;
  border-radius: 3px;
  color: white;
  font-weight: bold;
  height: 30px;
  width: 30px;
}

.default--erase-btn:hover {
  background: #ff3333;
}

.default--erase-btn:active {
  padding-top: 5px;
}
