@import url(https://fonts.googleapis.com/css2?family=Comforter&display=swap);
body {
  align-items: center;
  background: #dfe0e2;
  display: flex;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  justify-content: center;
  height: 100vh;
  margin: 0;
  width: 100vw;
}

.login-page__form {
  align-items: center;
  background: #071013;
  border-radius: 90px;
  display: flex;
  flex-direction: column;
  height: 25rem;
  justify-content: space-evenly;
  padding-bottom: 50px;
  width: 35rem;
}

.wallet__page {
  background: #dfe0e2;
  display: grid;
  grid-template: 12% / 25% 75%;
  grid-template-areas: 'header header' 'aside__menu expenditure__list';
  height: 100vh;
  width: 100vw;
}

.wallet__page--header {
  align-items: center;
  background: white;
  border-bottom: solid black 1px;
  display: flex;
  grid-area: header;
  justify-content: space-between;
}

.__header--section {
  display: flex;
}

.__header--div {
  align-items: center;
  background: #ced4da;
  border-radius: 10px;
  display: flex;
  margin: 10px;
}

.__header--title {
  color: #23b5d3;
  font-size: 40px;
  margin-left: 5%;
}

.__header--div p {
  padding-left: 15px;
}

.--div-span-email {
  border-radius: 10px;
  padding: 15px;
}

.--div-span-total {
  color: red;
  font-family: 'Comforter', cursive;
  font-size: 25px;
  margin: 0 10px;
}

.--div-span-currency {
  align-self: center;
  margin-right: 10px;
}

.wallet__page--menu-aside {
  background: #071013;
  color: white;
  grid-area: aside__menu;
  padding-top: 20px;
}

.--menu-aside-form {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.button {
  margin: 50px auto;
}

.wallet__page--table {
  border-collapse: collapse;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.table--header th {
  background: #75abbc;
  padding: 10px;
}

.table--body td {
  border-bottom: 1px solid #b9b5b5;
  font-family: Arial, Helvetica, sans-serif;
  padding: 7px;
  text-align: center;
}

.table--body tr:nth-child( even ) {
  background-color: #f2f2f2;
}

.table--body tr:hover {
  background: #23b5d3;
  color: #071013;
}

.default--title {
  color: #23b5d3;
  font-size: 40px;
}

.default--input {
  background: white;
  border: #23b5d3 solid 2px;
  border-radius: 7px;
  height: 30px;
  margin-top: 5px;
  padding: 7px;
}

.default--select {
  background: white;
  border: #23b5d3 solid 2px;
  border-radius: 7px;
  height: 45px;
  margin-top: 5px;
  padding: 7px;
}

.default--button {
  background: #23b5d3;
  border: none;
  border-radius: 7px;
  font-size: 16px;
  height: 35px;
  width: 170px;
}

.button {
  height: 50px;
}

.default--button:hover {
  background: #3cd0ee;
}

.default--button:active {
  border: white solid 1px;
  padding: 8px;
}

.default--input-label {
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont,
    Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell,
    Open Sans, Helvetica Neue, sans-serif Georgia , Times , serif;
  font-size: 18px;
  margin: 8px 5%;
  width: 40%;
}

.description {
  width: 100%;
}

.default--erase-btn {
  background: red;
  border: none;
  border-radius: 3px;
  color: white;
  font-weight: bold;
  height: 30px;
  width: 30px;
}

.default--erase-btn:hover {
  background: #ff3333;
}

.default--erase-btn:active {
  padding-top: 5px;
}

